import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

// Translation resources
const resources = {
  en: {
    translation: {
      // Navigation
      home: "Home",
      about: "About",
      services: "Services",
      careers: "Careers",
      contact: "Contact",

      // Services
      auditAssurance: "Audit & Assurance",
      taxation: "Taxation",
      businessSupport: "Business Support",
      managementConsultancy: "Management Consultancy",
      internationalTransactions: "International Transactions",
      newBusinessSupport: "New Business Support",

      // Hero section
      heroTitle: "Your business, Our advice It all sums up",
      whoWeAre: "Who we are",
      whatWeDo: "What we do",
      whoWeServe: "Who we serve",

      // Common
      loading: "Loading...",
      error: "Error",
      success: "Success"
    }
  },
  vi: {
    translation: {
      // Navigation
      home: "Trang ch",
      about: "Gii thiu",
      services: "Dch v",
      careers: "Tuyn dng",
      contact: "Lin h",

      // Services
      auditAssurance: "Kim ton & Bo m",
      taxation: "Thu",
      businessSupport: "H tr Kinh doanh",
      managementConsultancy: "T vn Qun l",
      internationalTransactions: "Giao dch Quc t",
      newBusinessSupport: "H tr Doanh nghip Mi",

      // Hero section
      heroTitle: "Doanh nghip ca bn, Li khuyn ca chng ti Tt c u tng hp",
      whoWeAre: "Chng ti l ai",
      whatWeDo: "Chng ti lm g",
      whoWeServe: "Chng ti phc v ai",

      // Common
      loading: "ang ti...",
      error: "Li",
      success: "Thnh cng"
    }
  }
};

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    resources,
    fallbackLng: 'en',
    debug: false,

    interpolation: {
      escapeValue: false, // React already does escaping
    },

    detection: {
      order: ['localStorage', 'navigator', 'htmlTag'],
      caches: ['localStorage'],
    },
  });

export default i18n;
